@import "variables";
@import "mixins";
@import "global";




#sidebar {
    display: none;
}

#sidebar-button-close {
    display: none;
}



.cover-img {
    display: flex;
    flex-direction: column;
    max-height: 100vh;
    max-width: 100vw;
    background: center no-repeat;
    text-align: center;
    color: white;
    justify-content: center;
    background-image: url("../assets/images/home-O67Y.webp");
  }
  .cover-img-transparency {
    background: rgba(black, 0.3);
  }
  .button {
    background-color: #67809f;
    color: white;
        

  }
  #consulation-arrow {
    margin-right: 20%;
    position: absolute;
  }
  .search-box {
    position: relative;
    width: 100vw;
    height: 40px;
    margin-bottom: 20px;
    
    
    input {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.1);
        border: 2px solid rgba(0,0,0,.1);
    }
    span {
        position: absolute;
        top: 0;
        right: 10px;
        transform: translateY(50%);
    }
}

.card-services {
    @include flex-center; 
    flex-direction: column;
    border: 1px solid lightgray;
    border-radius: 15px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 20px;
    background: #ffffff;

    a {
        font-size: 20px;
        padding: 10px 15px;
        margin-top: 10px;
        margin-bottom: 10px;
        border-radius: 5%;

    }

    small {
        margin: 0 20px;
    }

    &:hover {
        color: white !important;
        
        span, a { 
        background: white !important;
        }
        }

    span {
        border-radius: 50%;  
        width: 60px;
        height: 60px;    
        @include flex-center; 
        font-size: 35px;    
        color: white;  
    }

       
    &#bespoke-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $bespoke-color;
            color: $bespoke-color;
        }
        span, a {
            color: white;
        }

    }
    &#it-support-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $it-support-color;
            color: $it-support-color;
        }
        span, a {
            color: white;
        }

    }
    &#digital-marketing-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $digital-marketing-color;
            color: $digital-marketing-color;
        }
        span, a {
            color: white;
        }

    }
    &#telecoms-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $telecom-color;
            color: $telecom-color;
        }
        span, a {
            color: white;
        }

    }
    &#web-design-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $web-design-color;
            color: $web-design-color;
        }
        span, a {
            color: white;
        }

    }
    &#cyber-security-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $cyber-security-color;
            color: $cyber-security-color;
        }
        span, a {
            color: white;
        }

    }
    &#developer-training-card {
        span,
        a,
        &:hover, 
        &:hover span, 
        &:hover a {
            background-color: $developer-training-color;
            color: $developer-training-color;
        }
        span, a {
            color: white;
        }

    }

}




#email-signup {
    background-color: rgba(0,0,0,.1);
    width: 100vw;
    padding-top: 40px;
    h1 {
        font-size: 38px;
    }
    label {
        padding-top: 5px;
        font-size: 20px;
    }
    input {
        width: 100%;
        padding: 16px;
    }
    .required {
        color: red;
    }

}

#welcome-to-netmatters {
    background-color: rgb(46, 47, 54);
    padding-bottom: 60px;
    margin-bottom: 30px;
    text-decoration: none;
    color: white;
    h1 {
        font-size: 40px;
    }
    a {
        text-decoration: none;
        color: white;
    }
    p {
        font-size: 18px;
    }
    strong {
        font-size: 18px;
    }
}



#view-container, #view-container2 {
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 20px;

    a {
        text-decoration: none;
        color: black;
        font-weight: bold;
        font-size: 25px;
    }
    span {
        vertical-align: bottom;
        margin-bottom: 6.2px;
    }
}




#work-carousel {
    display: flex;
    padding: 40px 0px;
    size: 250px;


    img {
        padding: 0 20px;
        filter: grayscale(100%);
    }
    :hover {
        filter: grayscale(0%) !important;
    }
}

.button-primary {
    display: inline-block;
    padding: 10px 20px;
    margin-bottom: 20px;
    font-size: 30px;
    background-color: #272936;
    border-radius: 5px;
    box-sizing: border-box;
}

#stars {
    font-size: 55px;
}

#quote {
    font-style: italic;
    font-weight: bold;
    font-size: 24px;
}

#quote-author {
    font-size: 20px;
    font-weight: bold;
    color: grey;
}

#google {
    font-size: 20px;
    font-weight: bold;
    background: #4387f6;
    padding: 15px 20px;
    border-radius: 5px;
    span {
        padding-left: 5px;
        margin-bottom: 6.2px; 
        vertical-align: bottom;
    }
}

#trustpilot {
    font-size: 20px;
    font-weight: bold;
    background: #00b67b;
    padding: 15px 20px;
    border-radius: 5px;
    span {
        padding-left: 5px;
        margin-bottom: 6.2px; 
        vertical-align: bottom;
    }
}


