            background-color: $telecom-color;
        }
    }

    #coalition { 
        h2 {
            color: $bespoke-color;
        }
        .img-container {
            position: relative;
        }
       .news-button {
            position: absolute;
            
            font-size: 15px;
            color: white;
            padding: 2px 4px;
            background-color: $bespoke-color;
            right: 0px;
            top: -15px;
            border-radius: 0%;
        }
        a {
            background-color: $bespoke-color;
        }
        
    }    

    #jamie {
        h3 {
            color: $it-support-color;
        }
        .img-container {
            position: relative;
        }
       .news-button {
            position: absolute;
            
            font-size: 15px;
            color: white;
            padding: 2px 4px;
