        }
    }

    padding-bottom: 30px;

    .card-news {
        border: 1px solid rgba(207, 207, 207, 0.726);
        background-color: white;
        margin-top: 20px;
        .news-photo {
            width: 100%;
        }
        h3, h2 {
            font-size: 18px;
            justify-content: center;
            padding: 25px;
            .minute-read {
            color: grey;
            }
        }

        


    }
        
        p {
            justify-content: center;
            padding: 0px 25px;
            color: gray;
        }

        a {
            display: inline-block;
            font-weight: bold;
            margin: 25px 0 0 25px;
            padding: 10px 15px;
            border-radius: 5px;
            font-size: 18px;
        }


        .poster {
            padding-top: 32px;
            border-top: 1px solid rgba(207, 207, 207, 0.726);
            margin: 20px 0px 10px 0px;
            display: inline-flex;
            width: 100%;
            overflow: hidden;

            .avatar {
                border-radius: 50%;
                overflow: hidden;
                height: 50px;
                width: 50px;
                margin-left: 30px;
                margin-bottom: 20px;
            }
        }
}


#view-container2 {
    img {
        width: 60%;
    }
    margin-bottom: 50px;
}

#information {
    background-color: rgb(46, 47, 54);
    color: white !important;
    text-decoration: none;
    a {
        color: white;
    }
    ul {
        list-style-type: none;
        display: inline;
    }
}

footer {
    font-size: 15px;
    color: white;
    background-color: #212330;
}

.socials {

        display: grid;
        grid-template-areas: 
        "facebook linkedin"
        "twitter instagram";
        grid-template-columns: 20%;
        gap: 35px 40px;
        .social-button {
            padding: 20px 40px ;
            background-color: #212330;
            }
            i {
                position: relative;
                width: 14px;
                font-size: 30px;
                right: 6px;
                top: 5px;

        }
}

#follow-us-img {
    margin: 20px 0 10px 0;
    width: 75%;
}

.sticky {
    position: -webkit-sticky;
    position: -moz-sticky;
    position: -o-sticky;
    position: -ms-sticky;
    position: sticky;
    text-decoration: none;
    color: white;
    top: 0;
}

    #manage-consent {
        font-weight: bold;
        background-color: #75788a;
